.instructions {
  width: 100%;
  height: 100%;
  background-color: #E9E9E9;
  @include flex-center;
  flex-direction: column;
  text-align: center;

  p {
    font-weight: bold;
    margin-bottom: 20px;
  }

  button {
    border: 2px solid #222;
    font-family: inherit;
    font-size: inherit;
    color: inherit;
    background: none;
    cursor: pointer;
    padding: 12px 32px;
    display: inline-block;
    margin: 15px 30px;
    text-transform: uppercase;
    letter-spacing: 1px;
    font-weight: 700;
    outline: none;
    position: relative;
    transition: all 0.3s;

    &:hover {
      background-color: #222;
      color: #E9E9E9;
    }
  }
}