.exporter {
  display: flex;
  justify-content: space-around;
  background-color: #1A191A;
  padding: 20px;
  width: 100%;
  height: 100%;
  overflow: scroll;

  .column {
    flex: 1;
    height: 100%;
    max-width: 50%;
  }

  .input-wrapper {
    margin-top: 40px;
    display: flex;
    justify-content: space-around;
  }

  h1 {
    font-weight: 700;
    font-size: 20px;
    text-transform: capitalize;
    margin-bottom: 20px;
  }

  .pre-wrapper {
    background-color: black;
  }

  pre {
    display: inline-block;
    width: 100%;
    height: 100%;
    color: white;
    font-weight: normal;
    background-color: black;
    padding: 10px 20px;
    font-size: 16px;
    overflow: scroll;
  }

  .json-key, .json-value, .json-string {
    font-weight: 700;
    font-size: 14px;
  }

  .json-key {
    color: #B18ED0;
  }
  .json-value {
    color: #7B99D0;
  }
  .json-string {
    color: #DA953D;
  }

  .checkbox, .radio {
    opacity: 0;
    position: absolute;   
  }

  .checkbox, .checkbox-label {
    display: inline-block;
    vertical-align: middle;
    margin: 5px;
    cursor: pointer;
  }

  .checkbox-label {
    position: relative;
  }

  .checkbox + .checkbox-label:before {
    content: '';
    background: #fff;
    border: 2px solid #ddd;
    display: inline-block;
    vertical-align: middle;
    width: 20px;
    height: 20px;
    padding: 2px;
    margin-right: 10px;
    text-align: center;
  }

  .checkbox:checked + .checkbox-label:before {
    background: #222;
    box-shadow: inset 0px 0px 0px 4px #fff;
  }

  button {
    border: 2px solid #222;
    font-family: inherit;
    font-size: inherit;
    color: inherit;
    background: none;
    cursor: pointer;
    padding: 12px 32px;
    display: inline-block;
    margin: 15px 30px;
    text-transform: uppercase;
    letter-spacing: 1px;
    font-weight: 700;
    outline: none;
    position: relative;
    transition: all 0.3s;

    &:hover {
      background-color: #222;
      color: #E9E9E9;
    }
  }
}